import React, { useState } from "react";
import { Modal, Box } from "@mui/material";
import NormalButton from "../../../UI/common/NormalButton/NormalButton";
import "./styles.scss";
import ErrorOutlineOutlinedIcon from '@mui/icons-material/ErrorOutlineOutlined';
import CommonFieldsTable from "../CommonFieldsTable";
import { useForm } from "react-hook-form";

const CommonModal = ({ visible, modalLabel, handleClose, handleApply, selectedFlats, watch, handleCommonField = () => { }, loading }) => {

  const roomNumberType = watch("roomNumberType")
  const bedNumberType = watch("bedNumberType")
  const roomFormat = watch("roomFormat")
  const sequenceFormat = watch("sequenceFormat");
  const {
    register,
    setValue,
    control,
    watch: tempFormWatch,
    handleSubmit
  } = useForm({
    mode: "all",
    defaultValues: {
      "roomNumberType": roomNumberType,
      "bedNumberType": bedNumberType,
      "roomFormat": roomFormat,
      "sequenceFormat": sequenceFormat,
      "unitDetails": [
        {
          "flatName": "A1001",
          "numberOfRooms": "",
          "rooms": []
        }
      ]
    }
  });
  const [showExtendedColumns, setShowExtendedColumns] = useState(false);
  const [isFormCompleted, setIsFormCompleted] = useState(false);

  return (
    <div className="col-md-10 m-0 p-0">
      <Modal
        open={visible}
        onClose={handleClose}
        aria-labelledby="modal-title"
        aria-describedby="modal-description"
      >
        <Box
          sx={{
            position: "absolute",
            top: "50%",
            left: "50%",
            transform: "translate(-50%, -50%)",
            width: showExtendedColumns ? 1100 : 605,
            borderRadius: "16px",
            bgcolor: "background.paper",
            boxShadow: 24,
            p: 2,
          }}
        >
          <p id="modal-description" className="fw-600 ">
            {modalLabel}
          </p>
          <hr />
          <div className={`basicForm m-0  `}>
            <ErrorOutlineOutlinedIcon
              className={`iconbtn`}
              onClick={
                () => { }
                //handleAdd(i, key)
              }
            ></ErrorOutlineOutlinedIcon>
            <label>You are filling common fields for {selectedFlats.join(", ")}</label>
          </div>
          <div className={`col-md-8 table pt-3 `}>
            <CommonFieldsTable
              tabelHeader={"Room Number Format"}
              tabelHeader2={"Number of Rooms"}
              tabelHeader4={"Room Number"}
              tabelHeader5={"Occupancy"}
              tabelHeader6={"Number of Beds"}
              tabelHeader7={"Bed Number"}
              setShowExtendedColumns={setShowExtendedColumns}
              showExtendedColumns={showExtendedColumns}
              setIsFormCompleted={setIsFormCompleted}
              onSubmit={handleCommonField}
              register={register}
              setValue={setValue}
              control={control}
              watch={tempFormWatch}
            />
          </div>
          <hr />
          <div className="d-flex w-100 col-12 justify-content-end mt-4 ">
            <div className="col-3 mx-3 ">
              <NormalButton
                className={"cancelBtn w-100 "}
                label={"Cancel"}
                onClick={handleClose}
                disabled={loading}
              />
            </div>
            <div className="col-3 ">
              <NormalButton
                onClick={handleSubmit(handleApply)}
                className={"applyBtn w-100"}
                label={"Apply"}
                disabled={!isFormCompleted || loading}
              />
            </div>
          </div>
        </Box>
      </Modal>
    </div>
  );
};

export default CommonModal;
